import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import CssBaseline from '@material-ui/core/CssBaseline';
import './App.css';
import SignIn from './pages/SignIn';
import { Order } from './pages/Order';

function App() {
  const {
    location: { hostname },
  } = window;
  return (
    <Router
      basename={
        (hostname === 'localhost' || hostname === '127.0.0.1') && '/roomservice'
      }
    >
      <CssBaseline />
      <div>
        <Switch>
          <Route path="/order">
            <Order />
          </Route>
          <Route path="/">
            <SignIn />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
