import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
// import Grid from '@material-ui/core/Grid';
import { TextField } from '@material-ui/core';

const allergies = [
  { name: 'Honey', desc: 'itchiness, hives, swelling' },
  { name: 'Peanut', desc: 'eczema, sneezing, cardiac arrest' },
  {
    name: 'Lactose intolerance',
    desc: 'upset stomach, bloating, gas, diarrhea',
  },
];

const insuranceDetails = [
  { name: 'Name', detail: 'Blue Cross' },
  { name: 'ID', detail: '523A-23526-235-23' },
  { name: 'Subscriber', detail: 'John Smith' },
  { name: 'Expiry date', detail: '04/2024' },
];

const useStyles = makeStyles(theme => ({
  listItem: {
    padding: theme.spacing(0, 0),
  },
  total: {
    fontWeight: '700',
  },
  title: {
    marginTop: theme.spacing(2),
  },
  cContain: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  textField: {
    // marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: 200,
  },
  dietOrder: {
    marginTop: theme.spacing(1),
  },
}));

export function Review() {
  const classes = useStyles();
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  return (
    <React.Fragment>
      <Typography variant="h5" gutterBottom>
        Your Profile
      </Typography>
      <Typography variant="h6" gutterBottom>
        Demographics
      </Typography>
      <TextField
        id="name"
        label="Name"
        defaultValue="John Smith"
        margin="normal"
        className={classes.textField}
        InputProps={{
          readOnly: true,
        }}
      />
      <TextField
        id="dob"
        label="Date of Birth"
        defaultValue="01/01/2019"
        margin="normal"
        className={classes.textField}
        InputProps={{
          readOnly: true,
        }}
      />
      <TextField
        id="ssn"
        label="Social Security #"
        defaultValue="******1234"
        margin="normal"
        className={classes.textField}
        InputProps={{
          readOnly: true,
        }}
      />
      <TextField
        id="phone"
        label="Phone"
        defaultValue="(321) 321-4213"
        margin="normal"
        className={classes.textField}
        InputProps={{
          readOnly: true,
        }}
      />
      <Typography
        variant="body2"
        color="textSecondary"
        component="p"
        className={classes.dietOrder}
      >
        Dietary order
      </Typography>
      <Typography variant="body2" component="p">
        Diabetic - healthy-eating plan that's naturally rich in nutrients and
        low in fat and calories
      </Typography>
      <div className="cContain">
        <Typography variant="h6" gutterBottom>
          Allergies
        </Typography>
      </div>
      <List disablePadding>
        {allergies.map(({ name, desc }, index) => (
          <ListItem className={classes.listItem} key={index}>
            <ListItemText primary={name} secondary={desc} />
          </ListItem>
        ))}
      </List>
      <div className="cContain">
        <Typography variant="h6" gutterBottom>
          Insurance details
        </Typography>
      </div>
      <List disablePadding>
        {insuranceDetails.map(({ name, detail }, index) => (
          <ListItem className={classes.listItem} key={index}>
            <ListItemText primary={name} secondary={detail} />
          </ListItem>
        ))}
      </List>

      {/* <Grid container spacing={2}>
        <Grid item container direction="column" xs={12} sm={12}>
          <Typography variant="h6" gutterBottom className={classes.title}>
            Insurance details
          </Typography>
          <Grid container>
            {insuranceDetails.map(({ name, detail }) => (
              <React.Fragment key={name}>
                <Grid item xs={6}>
                  <Typography gutterBottom>{name}</Typography>
                </Grid>
                <Grid item xs={6}>
                  <Typography gutterBottom>{detail}</Typography>
                </Grid>
              </React.Fragment>
            ))}
          </Grid>
        </Grid>
      </Grid> */}
    </React.Fragment>
  );
}
